* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.form {
  justify-content: center;
  align-items: center;
  gap: 35px;
  margin-bottom: 20px;
  display: flex;
}

.form-input {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  transition: box-shadow .3s, border .3s;
}

.form-input:focus {
  border: none;
  outline: none;
  box-shadow: 0 0 15px rgba(0, 0, 0, .3);
}

.form-input:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.form-button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.form-button:hover {
  background-color: #388e3c;
}

.photos {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  padding: 0;
  display: grid;
}

.photo-item {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.photo-item img {
  width: 100%;
  height: auto;
  display: block;
}

.load-more {
  justify-content: center;
  display: flex;
}

.is-hidden {
  display: none;
}

.news-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 0;
  list-style: none;
  display: grid;
}

.news-card {
  background-color: #f9f9f9;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 8px rgba(0, 0, 0, .1);
}

.news-image img {
  width: 100%;
  height: auto;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  display: block;
}

.news-details {
  padding: 20px;
}

.news-title {
  margin-top: 0;
  font-size: 1.2rem;
}

.news-author, .news-publishedAt {
  color: #888;
  margin-bottom: 5px;
  font-size: .9rem;
}

.news-description {
  color: #333;
  font-size: 1rem;
  line-height: 1.4;
}

.news-url {
  text-align: right;
  color: #00f;
  margin-top: 10px;
  text-decoration: none;
  display: block;
}

